<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Hasil Ujian: {{ props.jadwalId }}</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content class="ion-padding">
      <h2>Daftar Hasil Ujian</h2>
      <!-- Tombol Ekspor ke Excel -->
      <ion-button expand="full" @click="exportToExcel">Ekspor ke Excel</ion-button>
      <ion-list v-if="hasilUjian.length > 0">
          <ion-item v-for="(hasil, index) in hasilUjian" :key="hasil.userId">
            <ion-label>
              <h3>{{ index + 1 }}. {{ hasil.fullName }}</h3>
              <p><strong>Sekolah:</strong> {{ hasil.schoolName }}</p>
              <p><strong>Skor TPS:</strong> {{ hasil.scoreTPS || 0 }}</p>
              <p><strong>Skor MM:</strong> {{ hasil.scoreMM || 0 }}</p>
              <p><strong>Skor LBIND:</strong> {{ hasil.scoreLBIND || 0 }}</p>
              <p><strong>Skor LBING:</strong> {{ hasil.scoreLBING || 0 }}</p>
              <p><strong>Total Skor:</strong> 
                <span :style="{ color: getScoreColor(hasil.totalScore) }">
                  {{ hasil.totalScore }}
                </span>
              </p>
              <p><strong>Rata-Rata Skor:</strong> 
                <span :style="{ color: getScoreColor(hasil.score) }">
                  {{ hasil.score }}
                </span>
              </p>
              <p>Dibuat pada: {{ hasil.createdAt ? formatDate(hasil.createdAt) : 'Tidak tersedia' }}</p>
            </ion-label>
          </ion-item>
        </ion-list>
      <p v-else>Tidak ada pengguna yang mengikuti ujian dengan jadwal ID ini.</p>
      <ion-button v-if="hasMoreData" expand="full" @click="loadMoreData">
        Muat Lebih Banyak
      </ion-button>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>
<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonItem,
  IonLabel,
  IonList,
  IonToast,
  IonButton,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { getFirestore, collection, query, where, getDocs, limit, startAfter, doc, getDoc } from "firebase/firestore";

import { firebaseApp } from "../firebase";
import * as XLSX from "xlsx"; // Import library xlsx

// Mengambil jadwalId dari props
const props = defineProps({
  jadwalId: String,
});

const db = getFirestore(firebaseApp);
const hasilUjian = ref([]);
const lastVisible = ref(null);
const hasMoreData = ref(true);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});


// Menambahkan logika perhitungan skor jika subkoleksi kosong
const fetchHasilUjian = async () => {
  try {
    const kelasId = "Jf9KAZkky7PJjXGd44vi"; // ID kelas sesuai
    const usersRef = collection(db, "users");

    const usersQuery = lastVisible.value
      ? query(usersRef, where("kelas", "==", kelasId), startAfter(lastVisible.value), limit(100))
      : query(usersRef, where("kelas", "==", kelasId), limit(100));

    const usersSnapshot = await getDocs(usersQuery);

    const hasilData = [];

    // Update dokumen terakhir untuk pagination
    if (usersSnapshot.docs.length > 0) {
      lastVisible.value = usersSnapshot.docs[usersSnapshot.docs.length - 1];
    } else {
      hasMoreData.value = false;
    }

    const resultsPromises = usersSnapshot.docs.map(async (userDoc) => {
      const userId = userDoc.id;
      const userData = userDoc.data();
      const fullName = userData.fullName || "Tidak Tersedia";
      const schoolName = userData.schoolName || "Tidak Tersedia";

      console.log(`Memproses userId: ${userId}, Nama: ${fullName}, Sekolah: ${schoolName}`);

      // Ambil hasil ujian dari tabel 'results'
      const resultsDoc = await getDoc(doc(db, "examResults", userId, "results", props.jadwalId));
      if (resultsDoc.exists()) {
        const resultData = resultsDoc.data();

        hasilData.push({
          userId,
          fullName,
          schoolName,
          scoreTPS: resultData.scoreTPS || 0,
          scoreMM: resultData.scoreMM || 0,
          scoreLBIND: resultData.scoreLBIND || 0,
          scoreLBING: resultData.scoreLBING || 0,
          totalScore: resultData.totalScore || 0,
          score: resultData.score || 0,
          createdAt: resultData.createdAt ? formatDate(resultData.createdAt) : "Tidak tersedia",
        });
      }
    });

    await Promise.all(resultsPromises);

    hasilUjian.value = hasilUjian.value.concat(hasilData);
    urutkanHasilUjian();

    if (hasilData.length === 0) {
      showToast("Tidak ada pengguna yang mengikuti ujian dengan jadwal ID ini.", "warning");
    }
  } catch (error) {
    console.error("Error fetching hasil ujian:", error);
    showToast("Terjadi kesalahan saat mengambil data hasil ujian.", "danger");
  }
};

const loadMoreData = async () => {
  if (lastVisible.value) {
    try {
      const kelasId = "Jf9KAZkky7PJjXGd44vi"; // ID kelas sesuai
      const usersRef = collection(db, "users");
      // Query pengguna berdasarkan pagination
      const usersQuery = query(usersRef, where("kelas", "==", kelasId), startAfter(lastVisible.value), limit(100));
      const usersSnapshot = await getDocs(usersQuery);
      const hasilData = [];

      // Update dokumen terakhir untuk pagination
      if (usersSnapshot.docs.length > 0) {
        lastVisible.value = usersSnapshot.docs[usersSnapshot.docs.length - 1];
      } else {
        hasMoreData.value = false;
      }

      const resultsPromises = usersSnapshot.docs.map(async (userDoc) => {
        const userId = userDoc.id;
        const userData = userDoc.data();
        const fullName = userData.fullName || "Tidak Tersedia";
        const schoolName = userData.schoolName || "Tidak Tersedia";

        console.log(`Memproses userId: ${userId}, Nama: ${fullName}, Sekolah: ${schoolName}`);

        // Ambil hasil ujian dari tabel 'results'
        const resultsDoc = await getDoc(doc(db, "examResults", userId, "results", props.jadwalId));
        if (resultsDoc.exists()) {
          const resultData = resultsDoc.data();

          hasilData.push({
            userId,
            fullName,
            schoolName,
            scoreTPS: resultData.scoreTPS || 0,
            scoreMM: resultData.scoreMM || 0,
            scoreLBIND: resultData.scoreLBIND || 0,
            scoreLBING: resultData.scoreLBING || 0,
            totalScore: resultData.totalScore || 0,
            score: resultData.score || 0,
            createdAt: resultData.createdAt ? formatDate(resultData.createdAt) : "Tidak tersedia",
          });
        }
      });

      await Promise.all(resultsPromises);

      hasilUjian.value = hasilUjian.value.concat(hasilData);
      urutkanHasilUjian();

      if (hasilData.length === 0) {
        showToast("Tidak ada data hasil ujian lebih lanjut.", "warning");
      }
    } catch (error) {
      console.error("Error loading more data:", error);
      showToast("Terjadi kesalahan saat memuat lebih banyak data.", "danger");
    }
  }
};

// Fungsi untuk menampilkan toast
const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

// Fungsi untuk menentukan warna berdasarkan skor
const getScoreColor = (score) => {
  if (score >= 80) {
    return "green"; // Skor tinggi
  } else if (score >= 60) {
    return "orange"; // Skor sedang
  } else {
    return "red"; // Skor rendah
  }
};

// Fungsi untuk format tanggal
const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
  return date.toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

// Fungsi untuk mengurutkan hasil ujian
const urutkanHasilUjian = () => {
  hasilUjian.value.sort((a, b) => b.score - a.score); // Urutkan berdasarkan skor (desc)
};

// Fungsi untuk mengekspor ke Excel
const exportToExcel = () => {
  const formattedData = hasilUjian.value.map((hasil, index) => ({
    No: index + 1,
    "Nama Lengkap": hasil.fullName,
    Sekolah: hasil.schoolName,
    "Skor TPS": hasil.scoreTPS || 0,
    "Skor MM": hasil.scoreMM || 0,
    "Skor LBIND": hasil.scoreLBIND || 0,
    "Skor LBING": hasil.scoreLBING || 0,
    "Total Skor": hasil.totalScore || 0,
    "Rata-Rata Skor": hasil.score || 0,
    "Tanggal Dibuat": hasil.createdAt ? formatDate(hasil.createdAt) : "Tidak tersedia",
  }));

  const ws = XLSX.utils.json_to_sheet(formattedData);
  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, "Hasil Ujian");
  XLSX.writeFile(wb, "hasil_ujian.xlsx");
};


onMounted(() => {
  fetchHasilUjian(); // Memuat hasil pertama kali
});
</script>



<style scoped>
h2 {
  margin-bottom: 20px;
}

ion-item {
  --background: #f9f9f9;
  --border-radius: 8px;
  --box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  padding: 12px;
  margin-bottom: 10px;
}

ion-label {
  padding-left: 12px;
}

p {
  font-size: 14px;
  color: #555;
}

ion-toast {
  --background: #4CAF50;
  --color: white;
}

ion-item ion-label h3 {
  font-size: 18px;
  font-weight: bold;
}

span {
  font-weight: bold;
  font-size: 16px;
}

span.green {
  color: green;
}

span.orange {
  color: orange;
}

span.red {
  color: red;
}
</style>
